import React, { Component } from 'react'
import axios from 'axios'
let Button = (props) => {
    return (
        <a href="/login" className="btn">
            <img src="/images/login.svg" alt="" />
        </a>
    )
}
let cleanToken = (e) => {
    return localStorage.removeItem('TOKEN_KEY')
}
let UserLogged = (props) => {
    return (
        <>
            Bem vindo(a) {props.name}
            <a href="/logout" onClick={cleanToken} className="btn btn-logout">
                <img src="/images/logout.svg" alt="Sair" Title="Sair" />
            </a>
        </>
    )
}
export class Profile extends Component {
    state = {
        logged: false,
        name: ''
    }
    componentDidMount() {
        let config = {
            mode: 'no-cors',
            headers: {
                "Authorization": `Bearer ${localStorage.getItem('TOKEN_KEY')}`,
                'Access-Control-Allow-Headers': '*'
            }
        }
        // let url = 'http://localhost:4003/api/verifyToken'
        let url = '/api/verifyToken'
        axios.get(url, config)
            .then(response => {
                if (response.data.isProtected) {
                    let name = response.data.data.user.name
                    this.setState({ logged: true, name: name })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }
    render() {

        return (
            <>
                {this.state.logged ? <UserLogged name={this.state.name} /> : <Button />}
            </>
        )
    }
}

export default Profile