import React from 'react'
import { Checkbox } from '../../Forms/Layout'
import { Consumer } from '../../../Contexts/CartContext'
let keyGenerator = () => {
    return '_' + Math.random().toString(36).substr(2, 9);
}
const DeliveryInfo = (props) => {
    let { results } = props;

    results = results ? results : []
    let key = keyGenerator()
    let title = results.length ? <h5> Frete </h5> : null
    return (
        <Consumer>
            {store => {
                console.log(store)
                let { handleStates } = store
                return (
                    <>
                        <hr />
                        {title}
                        <div key={key} style={{ display: 'flex' }} className="cart-list m-bottom-30">

                            {results.map((item, key) => {
                                let serviceType = item.Codigo === 40010 ? 'Sedex' : 'PAC'
                                return (
                                    <>
                                        <div key={keyGenerator()} style={{ width: '50%' }}>
                                            <strong>{serviceType}</strong> <br />
                                            Valor R${item.Valor} <br />
                                            Prazo: {item.PrazoEntrega} dias
                                            <Checkbox value="1" label={serviceType} handleInput={handleStates} />
                                        </div>
                                        <div>
                                        </div>

                                    </>
                                )
                            })}
                        </div>
                    </>
                )
            }}
        </Consumer>

    )
}

export default DeliveryInfo
