import React, { Component } from 'react'
import axios from 'axios'

class Index extends Component {
    state = {
        active: false
    }
    toggleSearch = (e) => {
        e.preventDefault()
        this.setState({ active: !this.state.active })
    }
    submitSearch = (e) => {
        e.preventDefault()
        axios.get('/search')
            .then(response => {
                console.log(response)
            })
            .catch(err => {
                console.log(err)
            })

        return false;
    }
    handleChange = (e) => {
        e.preventDefault()
        let { value, name } = e.target
        this.setState({ [name]: value })
    }
    render() {
        let wrapperCss = this.state.active ? 'search_wrapper active' : 'search_wrapper'
        return (
            <>
                <button className="btn" onClick={this.toggleSearch}>
                    <img src="/images/search.svg" alt="" />
                </button>
                <form className={wrapperCss} method="GET" action="/busca">
                    <div className="search-box">
                        <input type="text" name="terms" onChange={this.handleChange} placeholder="Digite aqui sua busca" />
                        <button className="btn btn-submit-search">
                            <img src="/images/search.svg" alt="" />
                        </button>
                    </div>
                </form>

            </>
        )
    }
}
export default Index