import React, { Component } from 'react'
import axios from 'axios';
import { InputCep, Button } from '../../Forms/Layout'

import DeliveryInfo from './DeliveryInfo'

export class CalcDelivery extends Component {
    state = {
        cep: '',
        btnDisabled: false,
        btnLabel: 'Calcular Frete'
    }
    handleChange = (e) => {
        e.preventDefault()
        let { name, value } = e.target;
        this.setState({ [name]: value }, () => {
            console.log(this.state)
        })
    }
    calcDeliveryHandler = (e) => {
        this.setState({ btnDisabled: true, btnLabel: 'Buscando...' })
        e.preventDefault()
        let { cep } = this.state;
        cep = cep.replace(/\./g, "")
        cep = cep.replace(/\-/g, "")

        let url = `http://localhost:4003/api/frete/1/${cep}/`;
        axios.get(url)
            .then(response => {
                let { data } = response;
                this.setState({
                    deliveryData: data,
                    btnDisabled: false,
                    btnLabel: 'Calcular Frete'
                })
            })
            .catch(err => {

            })
        return false;
    }
    render() {
        return (
            <>
                <div className="cart-finalize">
                    <div className="item-wrapper">
                        <InputCep css="w-100" name="cep" label="" placeholder="CEP" handleInput={this.handleChange} required={true} />
                    </div>
                    <div className="item-wrapper">
                        <Button css="w-100" label={this.state.btnLabel} action={this.calcDeliveryHandler} disabled={this.state.btnDisabled} />
                    </div>
                </div>
                <div className="info-wrapper">
                    {this.state.deliveryData && <DeliveryInfo results={this.state.deliveryData} />}
                </div>
            </>
        )
    }
}

export default CalcDelivery
