import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import { Text, Email } from '../../Forms/Layout'
import ModalMessage from './ModalMessage'
import './modal.css'
import axios from 'axios'

export default class ModalLead extends Component {
    state = {
        nome: '',
        email: '',
        status: ''
    }
    submitForm = (e) => {
        e.preventDefault()
        let data = {
            name: this.state.nome,
            email: this.state.email,
        }
        axios.post('/subscribe', { data: data })
            .then(response => {
                this.setState({
                    message: 'Obrigado, em breve você receberá nossas novidades em seu email',
                    status: true,
                })
            })
            .catch(err => {
                this.setState({
                    message: 'Estamos com algum problema, tente mais tarde',
                    status: true,
                })
            })

        return false;
    }
    handleInput = (e) => {
        e.preventDefault()
        this.setState({ [e.target.name]: e.target.value })
    }
    closeComponent = (e) => {
        e.preventDefault()

        var container = ReactDOM.findDOMNode(this).parentNode;
        ReactDOM.unmountComponentAtNode(container);
    }
    render() {
        let modalMessageTag = this.state.status ? <ModalMessage message={this.state.message} /> : null
        let imageSrc = this.state.status ? '/images/cancel-white.svg' : '/images/cancel.svg'
        return (
            <div className="modal-wrapper_lead">
                <div className="box">
                    <button className="cancel" onClick={this.closeComponent}>
                        <img src={imageSrc} alt="" />
                    </button>
                    {modalMessageTag}
                    <div className="box_left">
                        <span>MUSE</span>
                    </div>
                    <div className="box_right">
                        <h2>Receba dicas e novidades </h2>
                        <form className="box-content pattern-form" onSubmit={this.submitForm}>
                            <div className="form-group">
                                <Text name="nome" label="Nome:" handleInput={this.handleInput} required={true} />
                            </div>
                            <div className="form-group">
                                <Email name="email" label="Email:" handleInput={this.handleInput} required={true} />
                            </div>
                            <div className="col-sm-12">
                                <button className="btn btn-primary">Cadastrar</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}
