import React, { Component } from 'react'

export default class index extends Component {
    state = {
        active: false
    }
    toggleMenu = (e) => {
        e.preventDefault()
        this.setState({ active: !this.state.active })
    }
    hideMenu = (e) => {
        e.preventDefault()
        this.setState({ active: false })
    }
    render() {
        let navMenuCss = this.state.active ? 'menu-nav_section flex-center' : 'menu-nav_section'
        return (
            <>
                <button id="" className="btn" onClick={this.toggleMenu}>
                    <img src="/images/menu.svg" alt="" />
                </button>
                <div className={navMenuCss}>
                    <button className="btn btn-close-menu" onClick={this.hideMenu}>
                        <img src="/images/cancel.svg" alt="" />
                    </button>
                    <nav>
                        <ul>
                            <li>
                                <a href="/">Home</a>
                            </li>
                            <li>
                                <a href="/sobre">Sobre</a>
                            </li>
                            <li>
                                <a href="/busca">Produtos</a>
                            </li>
                            <li>
                                <a href="/contato">Contato</a>
                            </li>
                        </ul>
                    </nav>
                </div>
            </>
        )
    }
}
