import React, { Component } from 'react'
import { Consumer } from '../../../Contexts/CartContext'
import CartItem from './CartItem'
import CalcDelivery from './CalcDelivery'

import { makeid } from '../../../helpers/index'

class Index extends Component {
	state = {
		active: false
	}
	toggleCart = (e) => {
		e.preventDefault()
		this.setState({ active: !this.state.active })
		return false;
	}
	hideCart = (e) => {
		e.preventDefault()
		this.setState({ active: false })
	}

	render() {
		let { active } = this.state;
		let cartCss = active ? 'cart-list_menu active' : 'cart-list_menu'
		return (
			<>
				<button onClick={this.toggleCart} className="btn btn-cart">
					<img src="/images/shopping-cart.svg" alt="" />
				</button>
				<div className={cartCss}>

					<button className="btn close-cart" onClick={this.hideCart}>
						<img src="/images/cancel.svg" alt="" />Fechar
					</button>
					<Consumer>
						{store => {
							let { products } = store.state
							let { deleteProduct } = store;
							if (products.length) {
								let totalProducts = 0;

								return (
									<div className="cart-box">
										{products.map((item, key) => {
											totalProducts += parseFloat(item.price)
											return <CartItem deleteProduct={deleteProduct} key={makeid(4)} name={item.name} item_id={key} price={item.price} image={item.image} id={item.id} />
										})}
										<CalcDelivery />
										<div className="cart-finalize">
											<span>Total: R${totalProducts}</span> <span>|</span> <a href='/checkout'>Finalizar</a>
										</div>
									</div>
								)
							}
							else {
								return 'Cart is empty'
							}
						}}
					</Consumer>
				</div>
			</>
		)
	}
}

export default Index