export const InputCep = (props) => {
    let { css, label, name, handleInput, required } = props;
    css = css ? `${css} input-text_wrapper` : 'input-text_wrapper col-sm-12'
    let LabelTag = label ? <label> {label} </label> : null
    return (
        <div className={css}>
            {LabelTag}
            <InputMask alwaysShowMask={true} mask="99.999-999" name={name} type='tel' maskChar={null} onChange={handleInput} required={required} />
        </div>
    )
}
export const Button = props => {
    let { css, label, action, disabled } = props;
    css = css ? `${css} button_wrapper` : 'button_wrapper col-sm-12'

    return (
        <>
            <div className={css}>
                <button disabled={disabled} onClick={action ? action : null}> {label} </button>
            </div>
        </>
    )
}

export const Checkbox = (props) => {
    let { label, value, handleInput, disabled } = props;
    return (
        <div className="btn-wrapper">
            <button value={value} onClick={handleInput} disabled={disabled}>
                {label}
            </button>
        </div>
    )
}