import React from 'react'

let CartItem = (props) => {
    let { name, price, deleteProduct, item_id } = props
    return (
        <div className="cart-item">
            <div className="name"> {name} </div>
            <div className="price">
                <span>R${price}</span>
                <button className="cart-item_btn-icon" onClick={e => deleteProduct(e, item_id)}>
                    <img src="/images/cancel.svg" alt="" />
                </button>
            </div>

        </div>
    )
}
export default CartItem